.navbar{
    height: 50px;
    width: 100%;
    background-color: #4b7acd;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position:sticky;
    top:0;
    z-index: 123;
    box-shadow: 0 0 5px 1px #0d0d0d;
}
.navbar a {
    padding: 10px;
    color: white;
    text-decoration:none;
    font-size: 20px;
    font-family: cursive;
    font-weight: bolder;

}