.Footer_container{
    height: 100%;
    width: 100%;
    background-color: #4b7acd;
}
.Footer_container ul {
    padding: 10px 20px;
    list-style-type: none;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.Footer_container ul li{
    padding: 10px 20px;
    transition: 0.1s ease-in;
    border-radius: 10px;
}
.Footer_container ul li a{
    color: white;
    text-decoration: none;
    border-radius: 5px;
}
.Footer_container ul li:hover{
    background-color: #212121;
    cursor: pointer;
}