.container{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.card { 
    height: 300px;
    min-width: 300px;
    transform: scale(0.9);
    border-radius: 10px;
    transition: all 0.5s ease;

}
.card:hover{
    transform: scale(0.88);
    cursor: pointer;
}
.card h2{
    padding: 10px ;

}
.card img{
    height: 200px;
    width: 300px;
    border-radius: 10px;
    object-fit: cover;
}
@media only screen and (max-width: 1200px) {
    .container{
        justify-content: center;
    }
}