.Container{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Container h1{
    text-align: center;
    font-size: 50px;
    text-shadow: 0 0 5px 1px #0d0d0d;
}
.Container img{
    height: 100%;
    width: 100%;
    transform: scale(0.9);
    border-radius: 10px;
    box-shadow: 0 0 5px 1px #0d0d0d;
}

@media only screen and (max-width: 1200px) {

    .Container{
        flex-wrap: wrap;
        flex-direction: column-reverse;
    }
    .Container h1{
        font-size:30px;
    }
}